import type { ParagraphData } from '@ali/4ever-plugin-paragraph';

export function getSymbolAlign(listData: Required<ParagraphData>['list']) {
  const { listStyle, isTaskList } = listData;
  if (
    (listStyle &&
    ['upperRoman', 'lowerRoman'].includes(listStyle.format)) ||
    isTaskList
  ) {
    return 'right';
  } else {
    return 'left';
  }
}
